import {Component} from '@angular/core';
import {NavController} from 'ionic-angular';
import {UserRegister} from "../user-register/user-register "
import { ToastController } from 'ionic-angular';

import {Http,Headers} from '@angular/http';
import { interfaceUrls }from "../../providers/serviceUrls";//
import { Network } from '@ionic-native/network';
import { AlertController } from 'ionic-angular';    //

import {UserAgreement} from "../user-agreement/user-agreement"
import{servicesInfo} from"../../providers/service-info"
@Component({
    selector: 'page-UserRegInfo',
    templateUrl: 'UserRegInfo.html',
})

export class UserRegInfo {
    constructor(public navCtrl: NavController,
                public toastCtrl: ToastController,
                private http: Http,
                public alertCtrl: AlertController,
                private network: Network,
                public servicesInfo: servicesInfo,
                )
        {

        };
    userInfo = {
        phone: '',
        yzm: '',
        newPassWord: '',
        rePassWord: ''
    };
    errorTip = false;        //true
    errorTipMsg = "";

    hasClick=false;
    hq = "";
    num = 120;
    agreeI=false;    //

    hasSuccess=false;    //
    offline:boolean=false;

    checkNetwork(){
        let self = this;
    
        self.network.onDisconnect().subscribe(()=>{
              self.offline=true; 
              self.toast('');
        });
        self.network.onConnect().subscribe(()=>{
              self.offline=false; 
        });
    
      }    
    toast(actions){
        let toast = this.toastCtrl.create({
          message: actions,
          duration: 2000,
          position:'middle'

        });
        toast.present();
      }
//
    showAlert() {
        // console.log(this.agreeI,"??")
        // let alert = this.alertCtrl.create({
        //   title: '',
        //   subTitle: this.agreementInfo.agreement,
        //   buttons: ['']
        // });
        // alert.present();
        this.navCtrl.push(UserAgreement)
      }

    agree(){
        if(this.agreeI==true){
            this.errorTipMsg = "";
        }
    }
    ionViewDidLoad(){
          this.checkNetwork()        
    }

    next(){
        
        var pwdTest = /^(?![\d]+$)(?![a-zA-Z]+$)(?![-!@#$%^&*()_+.{}`=|\/\[\]\\\'?;\':,<>]+$)[\da-zA-Z-!@#$%^&*()_+.{}`=|\/\[\]\\\'?;\':,<>]{6,32}$/;
        if(!this.userInfo.phone){

            // this.errorTip = true;
            // this.errorTipMsg = "";
            this.toast("")
        } else if (!(/^1[34578]\d{9}$/.test(this.userInfo.phone))) {
            // this.errorTip = true;
            // this.errorTipMsg = "";
            this.toast("")
        } else if (this.userInfo.yzm == null || this.userInfo.yzm == "") {
            // this.errorTip = true;
            // this.errorTipMsg = "";
            this.toast("")
        } else if(this.userInfo.newPassWord.length ==0){
            this.toast("");
        }else if(this.userInfo.rePassWord.length==0){
            this.toast("");
        }else if (this.userInfo.newPassWord.length < 6 || this.userInfo.newPassWord.length > 32) {
            // this.errorTip = true;
            // this.errorTipMsg = "6-32";
            this.toast("6-23");
        } else if (!pwdTest.test(this.userInfo.newPassWord) || !pwdTest.test(this.userInfo.rePassWord)) {
            // this.errorTip = true;
            // this.errorTipMsg = "";
            this.toast("");
        } else if (this.userInfo.newPassWord != this.userInfo.rePassWord) {
            // this.errorTip = true;
            // this.errorTipMsg = "";
            this.toast("")
        } else if(this.agreeI==false){
            // this.errorTip = true;
            // this.errorTipMsg = "";
            this.toast("");
        }else{
            this.hasSuccess = true;
            this.servicesInfo.userPhone = this.userInfo.phone;
            if(this.offline == true){
                this.toast('');
                return;
           }
            let params={
                "data":{
                    "mobilePhone":this.userInfo.phone,
                    "pwd":this.userInfo.newPassWord,
                    "code":this.userInfo.yzm
                }
            }
                let self = this;
            this.http.post(interfaceUrls.addStoreRegistered,params)
            .map(res => res.json())
            .subscribe(function (data) {
                if(data.errorinfo==null){
                    console.log(data);
                    self.servicesInfo.userId = data.data.userId;
                    // self.errorTip = false;

                    console.log(">>")
                    self.navCtrl.push(UserRegister)
                    self.hasSuccess = false;
                }else{
                    self.hasSuccess = false;
                    self.toast(data.errorinfo.errormessage);
                }


            })

        }

         // this.navCtrl.push(UserRegister)        //

    }
    findPasswordCode(){
       
        if(!this.userInfo.phone) {
            // this.errorTip = true;
            // this.errorTipMsg = "";
            this.toast("");
        }else if (!(/^1[34578]\d{9}$/.test(this.userInfo.phone))) {
            // this.errorTip = true;
            // this.errorTipMsg = "";
            this.toast("")
        }else{
            let params = { "data":{
                "mobile": this.userInfo.phone,
            }};
            if(this.offline == true){
                this.toast('');
                return;
           }
            let self = this;
            this.toast("");
            this.http.post(interfaceUrls.getCode,params)
            .map(res => res.json())
            .subscribe(function (data) {
                if(data.errorinfo==null){
                    self.errorTip = false;
                    self.hasClick = true;
                    let timer = setInterval(() => {
                        if (self.num == 0) {
                            clearInterval(timer);
                            self.hasClick = false;
                            self.hq = "";
                            self.num = 120;
                            return;
                        }
                        self.num--;
                    }, 1000);
                }else{
                    // self.errorTip = true;
                    self.toast(data.errorinfo.errormessage)
                    // self.errorTipMsg = data.errorinfo.errormessage;
                }


            })

        }
       
    }

}



