import {Component} from '@angular/core';
import {NavController} from 'ionic-angular';
import {UserRegister} from "../user-register/user-register "
import { ToastController } from 'ionic-angular';

import {Http,Headers} from '@angular/http';
import { interfaceUrls }from "../../providers/serviceUrls";//
import { Network } from '@ionic-native/network';
import { AlertController } from 'ionic-angular';    //

import {UserAgreement} from "../user-agreement/user-agreement"
import{servicesInfo} from"../../providers/service-info";
import { urlService } from "../../providers/urlService";
@Component({
    selector: 'page-userPwdFind',
    templateUrl: 'userPwdFind.html',
})

export class UserPwdFind {
    constructor(public navCtrl: NavController,
                public toastCtrl: ToastController,
                private http: Http,
                public alertCtrl: AlertController,
                public urlService: urlService,
                public servicesInfo: servicesInfo,
                private network: Network,
                )
        {

        };
    userInfo = {
        phone: '',
        yzm: '',
        newPassWord: '',
        rePassWord: ''
    };
    errorTip = false;        //true
    errorTipMsg = "";

    hasClick=false;
    hq = "";
    num = 120;
 
    offline:boolean=false;

    toast(actions){
        let toast = this.toastCtrl.create({
          message: actions,
          duration: 3000,
          position:'bottom'

        });
        toast.present();
      }

      checkNetwork(){
        let self = this;
    
        self.network.onDisconnect().subscribe(()=>{
              self.offline=true; 
              self.toast('');
        });
        self.network.onConnect().subscribe(()=>{
              self.offline=false; 
        });
    
      }

    ionViewDidLoad(){
        this.checkNetwork()        
    }

    next(){
        var pwdTest = /^(?![\d]+$)(?![a-zA-Z]+$)(?![-!@#$%^&*()_+.{}`=|\/\[\]\\\'?;\':,<>]+$)[\da-zA-Z-!@#$%^&*()_+.{}`=|\/\[\]\\\'?;\':,<>]{6,32}$/;
        if(!this.userInfo.phone){

            // this.errorTip = true;
            // this.errorTipMsg = "";
            this.toast("")
        } else if (!(/^1[34578]\d{9}$/.test(this.userInfo.phone))) {
            // this.errorTip = true;
            // this.errorTipMsg = "";
            this.toast("")
        } else if (this.userInfo.yzm == null || this.userInfo.yzm == "") {
            // this.errorTip = true;
            // this.errorTipMsg = "";
            this.toast("")
        } else if (this.userInfo.newPassWord.length < 6 || this.userInfo.newPassWord.length > 32) {
            // this.errorTip = true;
            // this.errorTipMsg = "6-32";
            this.toast("6-23");
        } else if (!pwdTest.test(this.userInfo.newPassWord) || !pwdTest.test(this.userInfo.rePassWord)) {
            // this.errorTip = true;
            // this.errorTipMsg = "";
            this.toast("");
        } else if (this.userInfo.newPassWord != this.userInfo.rePassWord) {
            // this.errorTip = true;
            // this.errorTipMsg = "";
            this.toast("")
        } else{
            let params={
                "data":{
                    "mobile":this.userInfo.phone,
                    "newPwd":this.userInfo.newPassWord,
                    "code":this.userInfo.yzm
                }
            }
            if(this.offline == true){
                this.toast('');
                return;
           }
            let self = this;
            this.urlService.postDatas(interfaceUrls.findPwd,params)
            .then(function(resp){
                console.log(resp)
                if(resp){
                    if(resp.errorinfo==null){
                        console.log(resp.data);
                        self.toast(",")
                        localStorage.setItem("token","");
                        self.navCtrl.pop();
                    }else{
                        console.log(resp,"??")
                        // self.errorTip = true;
                        self.toast(resp.errorinfo.errormessage)
                        // self.errorTipMsg = data.errorinfo.errormessage;
                    }
                }
               
            });

        }

            //  this.navCtrl.push(UserRegister)

    }

    findPasswordCode(){
        this.toast("")
        if(!this.userInfo.phone) {
            // this.errorTip = true;
            // this.errorTipMsg = "";
            this.toast("");
        }else if (!(/^1[34578]\d{9}$/.test(this.userInfo.phone))) {
            // this.errorTip = true;
            // this.errorTipMsg = "";
            this.toast("")
        }else{
            let params = { "data":{
                "mobile": this.userInfo.phone,
            }};

            let self = this;
            this.urlService.postDatas(interfaceUrls.findPwdCode,params)
            .then(function(resp){
                if(resp.data.errorinfo==null){
                    self.errorTip = false;
                    self.hasClick = true;
                    let timer = setInterval(() => {
                        if (self.num == 0) {
                            clearInterval(timer);
                            self.hasClick = false;
                            self.hq = "";
                            self.num = 120;
                            return;
                        }
                        self.num--;
                    }, 1000);
                }else{
                    // self.errorTip = true;
                    self.toast(resp.data.errorinfo.errormessage)
                    // self.errorTipMsg = data.errorinfo.errormessage;
                }
           });




        }

    }

}



