import {Injectable} from "@angular/core";

@Injectable()
export class servicesInfo {
    token:any;
    userId:any;    //id
    areaId:any;    //Id
    longitude:any;    //
    latitude:any;    //
    address:any;        //
    pcar:any;        //
    userPhone:any;    //
    stroePerson:any;    //
    bankInfo:any;        ///
    orgId:any;
    registerNo="";    //
    creditCode="";     //
    
    firmName=null;        //
    operName=null;        //
    constructor() {
       this.token =  localStorage.getItem("token")
    }
    
}


